import React, { useState, useEffect } from 'react';
import { Box, Button, TextField, Switch, FormControlLabel, Typography, Tab, Tabs } from '@mui/material';

const TabPanel = ({ children, value, index, ...other }) => {
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`settings-tabpanel-${index}`}
      aria-labelledby={`settings-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ pt: 3 }}>{children}</Box>}
    </div>
  );
};

const a11yProps = (index) => {
  return {
    id: `settings-tab-${index}`,
    'aria-controls': `settings-tabpanel-${index}`,
  };
};

const Settings = () => {
  const [activeTab, setActiveTab] = useState(0);
  const [settings, setSettings] = useState({
    agent: {
      model: 'TinyLLaMA',
      usePhi2: false,
      temperature: 0.7
    },
    external: {
      apiKey: '',
      endpoint: ''
    },
    developer: {
      debugMode: false,
      logLevel: 'info'
    },
    scheduler: {
      enabled: true,
      pollingInterval: 60
    }
  });

  useEffect(() => {
    // Fetch settings on component mount
    fetchSettings();
  }, []);

  const fetchSettings = async () => {
    try {
      const response = await fetch('/api/settings');
      if (response.ok) {
        const data = await response.json();
        // Update settings state with fetched data
        setSettings(prev => ({ ...prev, ...data }));
      } else {
        console.error('Error fetching settings');
      }
    } catch (error) {
      console.error('Error fetching settings:', error);
    }
  };

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  const handleSettingChange = (category, field, value) => {
    setSettings(prev => ({
      ...prev,
      [category]: {
        ...prev[category],
        [field]: value
      }
    }));
  };

  const saveSettings = async () => {
    try {
      const response = await fetch('/api/settings', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(settings),
      });
      if (response.ok) {
        alert('Settings saved successfully');
      } else {
        console.error('Error saving settings');
        alert('Failed to save settings');
      }
    } catch (error) {
      console.error('Error saving settings:', error);
      alert('Failed to save settings');
    }
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Typography variant="h5" gutterBottom>
        Settings
      </Typography>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={activeTab} onChange={handleTabChange} aria-label="settings tabs">
          <Tab label="Agent Settings" {...a11yProps(0)} />
          <Tab label="External Services" {...a11yProps(1)} />
          <Tab label="MCP" {...a11yProps(2)} />
          <Tab label="Developer" {...a11yProps(3)} />
          <Tab label="Task Scheduler" {...a11yProps(4)} />
        </Tabs>
      </Box>

      <TabPanel value={activeTab} index={0}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, maxWidth: 600 }}>
          <TextField
            label="Model"
            value={settings.agent.model}
            onChange={(e) => handleSettingChange('agent', 'model', e.target.value)}
            fullWidth
          />
          <FormControlLabel
            control={
              <Switch
                checked={settings.agent.usePhi2}
                onChange={(e) => handleSettingChange('agent', 'usePhi2', e.target.checked)}
              />
            }
            label="Use Phi-2 Model"
          />
          <TextField
            label="Temperature"
            type="number"
            value={settings.agent.temperature}
            onChange={(e) => handleSettingChange('agent', 'temperature', parseFloat(e.target.value))}
            inputProps={{ step: 0.1, min: 0, max: 1 }}
            fullWidth
          />
        </Box>
      </TabPanel>

      <TabPanel value={activeTab} index={1}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, maxWidth: 600 }}>
          <TextField
            label="API Key"
            value={settings.external.apiKey}
            onChange={(e) => handleSettingChange('external', 'apiKey', e.target.value)}
            fullWidth
            type="password"
          />
          <TextField
            label="Endpoint URL"
            value={settings.external.endpoint}
            onChange={(e) => handleSettingChange('external', 'endpoint', e.target.value)}
            fullWidth
          />
          <Box sx={{ mt: 2, border: 1, borderColor: 'divider', p: 2, borderRadius: 1 }}>
            <Typography variant="h6" gutterBottom>
              Flare Tunnel
            </Typography>
            <Typography variant="body2" gutterBottom>
              Create a secure public URL to access your Agent Zero instance anytime, anywhere.
            </Typography>
            <Button variant="contained" sx={{ mt: 1 }}>
              Create Tunnel
            </Button>
          </Box>
        </Box>
      </TabPanel>

      <TabPanel value={activeTab} index={2}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, maxWidth: 600 }}>
          <Typography variant="body1">
            MCP settings will be configured here.
          </Typography>
        </Box>
      </TabPanel>

      <TabPanel value={activeTab} index={3}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, maxWidth: 600 }}>
          <FormControlLabel
            control={
              <Switch
                checked={settings.developer.debugMode}
                onChange={(e) => handleSettingChange('developer', 'debugMode', e.target.checked)}
              />
            }
            label="Debug Mode"
          />
          <TextField
            label="Log Level"
            value={settings.developer.logLevel}
            onChange={(e) => handleSettingChange('developer', 'logLevel', e.target.value)}
            fullWidth
          />
        </Box>
      </TabPanel>

      <TabPanel value={activeTab} index={4}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, maxWidth: 600 }}>
          <FormControlLabel
            control={
              <Switch
                checked={settings.scheduler.enabled}
                onChange={(e) => handleSettingChange('scheduler', 'enabled', e.target.checked)}
              />
            }
            label="Enable Task Scheduler"
          />
          <TextField
            label="Polling Interval (seconds)"
            type="number"
            value={settings.scheduler.pollingInterval}
            onChange={(e) => handleSettingChange('scheduler', 'pollingInterval', parseInt(e.target.value))}
            disabled={!settings.scheduler.enabled}
            fullWidth
          />
          <Box sx={{ mt: 2 }}>
            <Typography variant="h6" gutterBottom>
              Task Management
            </Typography>
            <Button variant="contained" sx={{ mt: 1 }}>
              New Task
            </Button>
          </Box>
        </Box>
      </TabPanel>

      <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
        <Button variant="contained" onClick={saveSettings}>
          Save Settings
        </Button>
      </Box>
    </Box>
  );
};

export default Settings;
